body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Nav{
    position: fixed;
    width : 300px;
    background: #222222;
    height: 100vh;
    font-family: tahoma;
    font-size: 15px;
    float: left;
}

.navLogo{
    margin : 15px 35px;
}

.navbar{
    margin: 0 10px;
}

.nav-link{
    color: #9b9b9b;
    font-weight: 500;
    margin: 2px 0;
}

.active{
    color: #fff;
}

.nav-link:hover{
    color: #fff;
}

.navbar-list:not(:last-child):after{
    content : '';
    width: 250px;
    height:2px;
    background: #2c2c2c; 
    display: block;
}

.menu-bar{
    display: none;
    position: absolute;
    top: 20px;
    right: 15px;
    z-index: 3;
}

.bar1, .bar2, .bar3{
    height : 5px;
    background: #fff;
    width: 25px;
    display:block;
    border-radius : 50px;
    margin: 4px 0;
    -webkit-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
}

.changebar1{
	-webkit-transform : rotate(-45deg) translate(-7px, 6px);
	        transform : rotate(-45deg) translate(-7px, 6px);
}

.changebar2{
	opacity: 0;
}

.changebar3{
	-webkit-transform : rotate(45deg) translate(-6px,-5px);
	        transform : rotate(45deg) translate(-6px,-5px);
}



@media screen and (max-width: 1300px) and (min-width: 768px){
    .Nav{
        width : 250px;
    }    
    
    img.navLogo{
        margin : 15px 25px;
        width: 200px;
    }
    
    .navbar{
        margin: 0 10px;
    }
    
    .navbar-list:not(:last-child):after{
        width: 200px;
    }
    
}

@media screen and (max-width: 768px){
    .Nav{
        position: relative;
        width: 100%;
        height: 70px;
    }   

    img.navLogo{
        margin : 2px 10px;
        width:170px;
    }
    .navbar{
        display: none!important;
        background: #222222;
        margin: 0;
        width: 250px;
        height: 100vh;
        z-index: 2;
    }
    .show{
        display: block!important;   
    }
    
    .navbar-nav{
        padding: 0px 10px!important;
    }

    .navbar-list:not(:last-child):after{
        width: 200px;
    }
    .menu-bar{
        display: block;
    }
    .overlay {
        position: fixed;
        display: none;
        width: 100%;
        height: 100%;
        top: 70px;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,.8);
        z-index: 1;
    }
}
.Banner{
    margin-left: 300px;
}

.banner-container{
    padding: 15px;
    background: #e8e8e6;
}
.hero-image{
    background: -webkit-linear-gradient(
        rgba(0, 0 , 0, 0.5),
        rgba(0, 0 , 0, 0.5)
    ),url(/dinnergise/static/media/banner.d0b236c4.jpg) 0 -50px no-repeat;
    background: linear-gradient(
        rgba(0, 0 , 0, 0.5),
        rgba(0, 0 , 0, 0.5)
    ),url(/dinnergise/static/media/banner.d0b236c4.jpg) 0 -50px no-repeat;
    height: 400px;
    background-size: cover;
}

.hero-quote{
    color: #fff;
    padding-top: 100px;
}

.hero-quote h1{
    font-weight: 600;
}

.hero-quote p{
    font-size: 20px;
    font-weight: 400;
}

button.btn-order{
    color: #fff;
    background: none;
    border: 2px solid #fff;
    border-radius: 30px;
    font-size: 14px;
    padding: 10px 25px;
    font-weight: 600;
}

.circle{
    height: 12px;
    width: 12px;    
    border-radius: 50%;
    border: 1px solid #fff;
    display: inline-block;
    margin: 3px;
    box-sizing: border-box;
}

.circle.active{
    background: #fff;
}

.slideIcon{
    text-align: center;
    margin-top: 50px;
}

@media screen and (max-width: 1300px) and (min-width: 768px){
    .Banner{
        margin-left: 250px;
    }    
    .hero-image{
        background-position: 0 0;       
    }
}

@media screen and (max-width: 991px) and (min-width: 768px){
    .banner-container{
        padding: 15px;
    }
    .hero-image{
        height: 300px;
        background-position: 0 0;       
    }
    .hero-quote{
        padding-top: 50px;
    }
    .hero-quote h1{
        font-size: 28px;
    }
    .hero-quote p{
        padding: 10px;
        font-size: 18px;
    }
    button.btn-order{
        font-size: 12px;
        padding: 5px 20px;
    }
    .slideIcon{
        margin-top: 30px;
    }
    .circle{
        height: 10px;
        width: 10px;           
    }
}

@media screen and (max-width: 768px) {
    .Banner{
        margin-left: 0px;
