    }
}

@media screen and (max-width: 450px){
    .hero-image{
        height: 500px;
        background-position: -60px 0px;
    }
    .hero-quote{
        padding: 100px 0;
    }
    .hero-quote h1{
        font-size: 28px;
        margin-bottom: 20px;
    }
    .hero-quote p{
        font-size: 18px;
    }
    button.btn-order{
