.Article{
    margin-left: 300px; 
}

.article-container{
    padding: 0px 15px 15px 15px;
    background: #e8e8e6;
}

.article-content{
    height: 250px;
    background: #fff;
    padding : 25px 10px;
}

.article-content h4{
