}
.leftside-article{
    text-align: left;
}
.leftside-article img{
    width: 25px;    
}

.rightside-article {
    background: #762484;
    height: 200px;
    padding: 15px;
    color: #fff;
}

.rightside-article h5:after{
    content : '';
    height: 2px; 
    width: auto;
    background: #fff;
    margin-top: 15px;
    display: block;

}

.rightside-article p{
    margin-bottom: 5px;
    font-size: 14px;
}

@media screen and (min-width: 1400px) {
